.datetime-input {
  background-color: var(--bg-color);
  border-radius: var(--border-radius);
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.datetime-input__now-button {
  background-color: transparent;
  border: none;
  color: var(--secondary-text-color);
  font-weight: bold;
  padding: 0 .8rem;
}

.datetime-input__now-overlay {
  -ms-flex-align: center;
      align-items: center;
  background-color: var(--bg-color);
  bottom: .8rem;
  display: -ms-flexbox;
  display: flex;
  left: 2.4rem;
  pointer-events: none;
  position: absolute;
  right: .8rem;
  top: .8rem;
}



